export default function getRuColor(eng) {
  let ruColor;
  switch (eng.toLowerCase().trim()) {
    case 'white':
      ruColor = '';
      break;
    case 'black':
      ruColor = '';
      break;
    case 'red':
      ruColor = '';
      break;
    case 'blue':
      ruColor = '';
      break;
    case 'green':
      ruColor = '';
      break;
    case 'yellow':
      ruColor = '';
      break;
    case 'purple':
      ruColor = '';
      break;
    case 'pink':
      ruColor = '';
      break;

    default:
      ruColor = eng;
      break;
  }
  return ruColor;
}
